<template>
  <span @click="clickLabel" class="label-item">{{labelName.label_cn || labelName.label}}</span>
</template>

<script>
import bus from '../../../assets/js/bus'
export default {
  name: 'Goods-Label',
  props: {
    labelName: ''
  },
  methods: {
    clickLabel () {
      bus.$emit('selectLabel', this.labelName)
    }
  }
}
</script>

<style lang="less" scoped>
  .label-item{
    padding: 5px;
    display: block;
    font-size: 12px;
    &:nth-child(2n) {
      background: #fafafa;
    }
  }
</style>



