<template>
  <div class="label-rank">
    <div class="title" @click="clickLabel">{{rank.category_cn || rank.label}}</div>
    <div class="labels">
      <goods-label
        v-for="(item, index) in rank.labels || rank.children"
        :key="index"
        :labelName="item"
      ></goods-label>
    </div>
  </div>
</template>

<script>
import bus from '../../../assets/js/bus'
import GoodsLabel from './goods-label.vue'
export default {
  name: 'Goods-Label-Rank',
  props: {
    rank: ''
  },
  methods: {
    clickLabel () {
      bus.$emit('selectLabel', this.rank)
    }
  },
  components: {
    GoodsLabel
  }
}
</script>

<style lang="less" scoped>
  .label-rank{
    .title{
      background: #e7e7e7;
      padding: 6px 15px;
      border-bottom-color: #eee;
      color: #777;
      border-color: #ddd;
      border-bottom: 1px solid transparent;
      border-top-left-radius: 3px;
      border-top-right-radius: 3px;
    }
  }
</style>



