<template>
  <router-link :to="'/mall/item/' + goodsDetail.mall_id" class="commodity">
    <div class="commodity-wrap">
      <div class="com-img">
        <img v-lazy="goodsDetail.imgurl || '/static/img/default-bg.jpg'" alt="商品">
      </div>

      <div class="infos text-nowrap">
        <div class="price">
          <span v-show="goodsDetail.label_parents === '配件'">建议零售价:</span><span class="price-num">￥{{goodsDetail.zc?goodsDetail.price_min + '万': goodsDetail.price_min}}</span>
          <small mute>{{goodsDetail.labels_name}}</small>
        </div>
        <div class="name">
          <span v-show="goodsDetail.labels_name === '配件'" style="font-size: 14px; color: #999;">图号:{{goodsDetail.spare_parts_code || '暂无数据'}}</span>
          <span class="text-nowrap">{{goodsDetail.name}}</span>
        </div>
      </div>
    </div>
  </router-link>
</template>

<script>
  export default {
    name: 'Commodity',
    props: {
      goodsDetail: {
        type: Object
      }
    }
  }
</script>

<style lang="less" scoped>
  .commodity{
    width: 49%;
    float: left;
    margin: 0 1% 1% 0;
    cursor: pointer;
    color: #333;
    &:nth-child(2n) {
      margin-right: 0;
    }
    .commodity-wrap{
      border: 1px solid #ddd;
      padding: 2px;
      background-color: #fff;
      &:hover{
        border: 1px solid #888;
      }
      .com-img{
        background: #ccc;
        height: 158px;
        img {
          width: 100%;
          height:100%;
          border: 0;
        }
      }
      .infos{
        padding: 10px;
        font-size: 16px;
        /*min-height: 63px;*/
        .price {
          span.price-num {
            color: maroon;
          }
        }
        .info {
          display: block;
          color: rgb(136, 136, 136);
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }
</style>



