<template>
  <div class="goods-wrap" :class="{'faxid': faxed}">
    <app-header
      title="全部商品"
      showBack="true"
      showSliders="true"
    ></app-header>
    <div class="clearfix ios-s goods-main"
         v-infinite-scroll="loadMore"
         infinite-scroll-disabled="loading"
         infinite-scroll-distance="100"
         infinite-scroll-immediate-check="testload"
    >
      <goods-item
        v-for="(item, index) in productList"
        :key="index"
        :goodsDetail="item"
      ></goods-item>
    </div>
    <mt-popup
      v-model="popupVisible"
      pop-transition="popup-fade"
      position="right"
      class="ios-s"
      style="height: 100%; width: 70%; z-index: 99999; overflow-x: scroll;">
      <div class="rank-content">
        <div class="sel">
          <div class="title" @click="getAll">所有商品</div>
          <input type="text" v-model="keyword" placeholder="商品名称或图文号">
          <a href="javascript:void (0)" @click="search">搜索</a>
        </div>
        <goods-rank
          v-for="(item, index) in labels"
          :key="index"
          :rank="item"
        ></goods-rank>
      </div>
    </mt-popup>
  </div>
</template>

<script>
import AppHeader from '../../../components/header/header.vue'
import GoodsItem from './goods.item.vue'
import GoodsRank from './goods-label-rank.vue'
import bus from '../../../assets/js/bus'
import {getMallProductList, getMallLabalList} from '../../../api/mall'
export default {
  name: 'Goods',
  data () {
    return {
      popupVisible: false,
      isModal: false,
      productList: [],
      labels: '',
      label: '',
      faxed: false,
      keyword: '',
      loading: false,
      page: 1,
      testload: true
    }
  },
  created () {
    this.$indicator.open({
      text: '加载中'
    })
    this.getDatas()
    this.getLabels()
  },
  watch: {
    popupVisible: function (val) {
      if (!val) {
        this.faxed = false
      }
    }
  },
  mounted () {
    this.$on('clickSliders', () => {
      this.popupVisible = true
      this.faxed = true
    })
    bus.$on('selectLabel', label => {
      this.$set(this, 'popupVisible', false)
      this.$set(this, 'label', label.label_id)
      this.$indicator.open({
        text: '加载中'
      })
      getMallProductList(undefined, this.keyword, label.label_id)
        .then(data => {
          this.$set(this, 'productList', data.data.list)
          this.$indicator.close()
          if (data.page <= 50) {
            this.loading = true
          }
        })
    })
  },
  methods: {
    getDatas () {
      this.$set(this, 'popupVisible', false)
      if (this.$route.params.id === 'all') {
        this.$set(this, 'labels', '')
      } else if (this.$route.params.id === 'fitting') {
        this.$set(this, 'labels', 35)
      } else if (this.$route.params.id === 'vehicle') {
        this.labels = '52'
      } else if (typeof parseInt(this.$route.params.id) === 'number') {
        this.labels = this.$route.params.id
      }
      getMallProductList(this.page, this.keywords, this.labels)
        .then(data => {
          if (data.state) {
            this.$set(this, 'productList', data.data.list)
            this.$indicator.close()
            if (data.page <= 50) {
              this.loading = true
            }
          } else {
            this.$toast(data.errormsg || '网络错误')
          }
        })
        .catch(() => {
          this.$indicator.close()
          this.$toast('网络错误')
        })
//      fitting 修改
    },
    getAll () {
      this.$indicator.open({
        text: '加载中'
      })
      getMallProductList()
        .then(data => {
          if (data.state) {
            this.$set(this, 'productList', data.data.list)
            this.$indicator.close()
            this.popupVisible = false
          } else {
            this.$toast(data.errormsg || '网络错误')
            this.popupVisible = false
          }
        })
        .catch(() => {
          this.$indicator.close()
          this.$toast('网络错误')
        })
    },
    getLabels () {
      getMallLabalList()
        .then(data => {
          if (data.state) {
            this.$set(this, 'labels', data.data.rank)
          } else {
            this.$toast(data.errormsg || '网络错误')
          }
        })
    },
    search () {
      this.$set(this, 'popupVisible', false)
      this.$indicator.open({
        text: '加载中'
      })
      getMallProductList(undefined, this.keyword, undefined)
        .then(data => {
          this.$set(this, 'productList', data.data.list)
          this.$indicator.close()
        })
    },
    loadMore () {
      this.$set(this, 'page', (this.page + 1))
      this.loading = true
      this.$indicator.open({
        text: '加载中'
      })
      setTimeout(() => {
        getMallProductList(this.page, this.keywords, this.label)
          .then(data => {
            if (data.state) {
              this.productList.push(...data.data.list)
              this.$indicator.close()
              if (data.page >= 50) {
                this.loading = true
              }
              if (data.data.list.length === 0) {
                this.loading = true
                return
              }
            } else {
              this.$toast(data.errormsg || '网络错误')
            }

            this.loading = false
          })
          .catch(() => {
            this.$indicator.close()
            this.$toast('网络错误')
          })
      })
    }
  },
  components: {
    AppHeader,
    GoodsItem,
    GoodsRank
  }
}
</script>

<style lang="less" scoped>
  .goods-wrap{
    width: 100%;
    position: absolute;
    padding-top: 50px;
    overflow: hidden;
    background-color: #e5e5e5;
    &:after {
      display: block;
      width: 100%;
      height: 5rem;
      font-size: .8rem;
      content: '到底部了';
      border-bottom: 1px solid #ddd;
      color: #999;
      text-align: center;
      .goods-main{
        display: flex;
      }
    }
  }
  .faxid {
    position: fixed;
  }
  .rank-content{
    padding-bottom: 60px;
    .sel{
      .title{
        background: #e7e7e7;
        padding: 6px 15px;
        border-bottom-color: #eee;
        color: #777;
        border-color: #ddd;
        border-bottom: 1px solid transparent;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
      }
      input{
        display: inline-block;
        height: 20px;
        padding-left: 1rem;
        background-color: transparent;
        font-size: 14px;
        border: 1px solid #ddd;
        border-radius: 0;
        vertical-align: middle;
      }
      a{
        color: #333;
        vertical-align: middle;
      }
    }
  }
</style>



